.place__like-button {
  border: none;
  background: url(../../../images/like.svg) center no-repeat;
  background-size: 22px 19px;
  object-fit: cover;
  padding: 0;
  width: 22px;
  height: 19px;
  transition: opacity 0.5s ease;
}

.place__like-button:hover {
  opacity: 0.5;
  cursor: pointer;
}